body {
  font-family: 'Nunito Sans', sans-serif;
  width: 100vw;
  height: calc(100vh - 62px);
  margin: 0;
  background-color: black;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

h1, h2, h3, h4, h5, h6, ul {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: white;
  cursor: pointer;
}

h1 {
  font-size: 44px;
  letter-spacing: 3px;
}

h1, h2, h3 {
  text-transform: uppercase;
}

h1, h2, h3, h4 {
  font-weight: 300;
}

h3 a {
  font-weight: 400;
}

.ib {
  display: inline-block;
}

.vt {
  vertical-align: top;
}

.vm {
  vertical-align: middle;
}

header {
  background-color: black;
  color: white;
  height: 62px;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 100;
  -webkit-box-shadow: 1px 1px 5px 1px rgba(0,0,0,0.3);
          box-shadow: 1px 1px 5px 1px rgba(0,0,0,0.3);
}

.container {
  width: 90%;
  max-width: 1000px;
  display: block;
  position: relative;
  margin: 0 auto;
}

.container .image-wrapper {
  top: 30px;
  width: 50%;
  height: auto;
  vertical-align: bottom;
}

.container .image-wrapper img {
  width: 100%;
  height: auto;
}

.content-wrapper {
  width: 1000px;
  height: calc(100vh - 62px);
  position: relative;
  display: block;
  margin: 0 auto;
}

.content-wrapper.noscroll {
  overflow-y: hidden;
}

.title {
  height: 100%;
  line-height: 62px;
  letter-spacing: 1.3px;
  margin: 0 25px;
}

.header-nav {
  position: absolute;
  line-height: 62px;
}

.header-nav-social {
  right: 15px;
}

.header-nav-li {
  text-transform: uppercase;
  font-size: 14px;
}

.header-nav-li a {
  color: white;
  padding: 20px;
}

.header-nav-li a:hover {
  border-bottom: 1px solid white;
}

.main-container {
  width: 100%;
  height: calc(100vh - 62px);
  margin-top: 62px;
  background-image: url('https://i.imgur.com/3ttb9Jb.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

.videos-container, .about-container {
  height: auto;
  padding-top: 50px;
  padding-bottom: 200px;
}

.shows-wrapper {
  background-color: rgba(0, 0, 0, 0.4);
  position: absolute;
  width: 80%;
  left: 10%;
  top: 100px;
  padding: 5px 15px;
  text-align: center;
  overflow-y: scroll;
}

.shows-internal {
  postion: relative;
}

.about-container::after, .videos-container::after {
  width: 100%;
  height: 100%;
  content: "";
  display: block;
  position: fixed;
  top: 0;
  background-color: black;
  z-index: 1;
  opacity: 0.85;
}

.videos-container::after {
  background-color: white;
}

.internal-container {
  position: relative;
  width: 100%;
  z-index: 2;
}

.news-container {
  background-color: rgba(0, 0, 0, 0.4);
  position: absolute;
  padding: 15px;
  width: 330px;
  min-height: 300px;
  max-height: 300px;
  right: 0;
  top: 100px;
  color: white;
  font-weight: 300;
  -o-text-overflow: clip;
     text-overflow: clip;
  -webkit-transition: 0.3s ease all;
  -o-transition: 0.3s ease all;
  transition: 0.3s ease all;
}

.news-container a {
  font-weight: bolder;
}

.news-container.about {
  width: 500px;
  -o-text-overflow: hidden;
     text-overflow: hidden;
  min-height: 600px;
  max-height: 600px;
}

.news-container.contact {
  min-height: 240px;
  max-height: 240px;
}

.news-card {
  margin-bottom: 20px;
}

.news-header {
  text-transform: uppercase;
}

.news-list {
  margin-left: 20px;
}

.news-list-li {
  list-style-type: none;
}

.news-container a:hover {
  text-decoration: underline;
}

/* ABOUT PAGE */

.bio-card {
  width: 90%;
  margin: 0 auto;
  display: block;
  margin-top: 50px;
}

.image-wrapper {
  position: absolute;
  height: 600px;
  top: 100px;
  -webkit-transition: 0.3s ease all;
  -o-transition: 0.3s ease all;
  transition: 0.3s ease all;
}

.image-wrapper.hide {
  top: 100vh;
}

.ltmg {
  margin-left: 5%;
  margin-right: 5%;
}

.image-wrapper img {
  height: 100%;
}

.bio-wrapper-wrapper {
  border: 1px solid white;
  padding: 15px;
  width: 50%;
  background-color: rgba(0, 0, 0, 0.3);
}

.bio-wrapper {
  color: white;
}

/* VIDEO PAGE */

.video-card {
  display: block;
  margin: 0 auto;
  width: 90%;
  text-align: center;
}

.video-card-other {
  margin-top: 50px;
}

.video-wrapper {
  margin-top: 20px;
}

.featured-vid {
  width: 720px;
  height: 405px;
  -webkit-box-shadow: 1px 1px 5px 1px rgba(0,0,0,0.3);
          box-shadow: 1px 1px 5px 1px rgba(0,0,0,0.3);
}

.music-links-wrapper {
  position: absolute;
  right: 0;
  top: 30px;
  width: 45%;
  display: inline-block;
}

.music-links-wrapper .more {
  color: black;
}

.music-links-wrapper .title-wrapper {
  width: 100%;
  margin-bottom: 20px;
  text-align: center;
}

.music-links-wrapper .title-wrapper h2 {
  font-size: 32px;
}

.text-wrapper a:hover {
  text-decoration: underline;
}

.music-links a {
  display: block;
  width: 100%;
  padding: 10px 0;
  border: 1px solid black;
  color: black;
  text-align: center;
  margin-bottom: 15px;
}

@media only screen and (max-width: 600px) {
  .content-wrapper {
    width: 100%;
  }

  .container .image-wrapper {
    width: 100%;
    top: 0px;
    position: relative;
  }

  h1 {
    font-size: 32px;
    letter-spacing: 3px;
  }

  .header-nav-nav {
    width: 100%;
    height: 0;
    overflow: hidden;
    position: absolute;
    background-color: rgba(0,0,0,0.95);
    left: 0;
    top: 62px;
    text-align: center;
    line-height: 42px;
    -webkit-transition: .3s ease all;
    -o-transition: .3s ease all;
    transition: .3s ease all;
  }

  .header-nav-nav.expanded {
    height: 100vh;
  }

  .header-nav-li.header-nav-li-nav {
    width: 100%;
    margin: 0 0;
    padding: 3% 0;
  }

  .header-nav-li {
    margin: 0 7px;
    line-height: 42px;
    font-size: 18px;
  }

  .header-nav-li a {
    padding: 10px;
  }

  .main-container {
    background-position: left;
  }

  .videos-container, .about-container {
    padding-top: 42px;
  }

  .videos-container {
    padding-top: 20px;
  }

  .music-links-wrapper {
    position: relative;
    width: 100%;
    text-align: justify;
  }

  .internal-container {
    padding-top: 0;
  }

  .news-container {
    top: auto;
    right: 0;
    bottom: 0;
    width: 100%;
    padding: 0;
    min-height: 350px;
    max-height: 350px;
    text-align: center;
  }

  .news-container.about {
    position: relative;
    width: 100%;
  }
  .image-wrapper {
    position: relative;
  }

  .news-card {
    padding: 20px;
    margin: 0;
  }

  .news-card:not(:last-of-type) {
    padding-bottom: 20px;
    border-bottom: 1px solid white;
  }

  .bio-card {
    width: 90%;
    margin-top: 0;
  }

  .image-wrapper, .bio-wrapper-wrapper {
    width: 100%;
    margin: 0;
  }

  .bio-wrapper-wrapper {
    margin-top: 20px;
    text-align: center;
  }

  .featured-vid {
    width: 100%;
    height: 250px;
    -webkit-box-shadow: 1px 1px 5px 1px rgba(0,0,0,0.3);
            box-shadow: 1px 1px 5px 1px rgba(0,0,0,0.3);
  }

  .video-card h2 {
    font-size: 18px;
  }

  .video-card-other {
    margin-top: 20px;
  }
}



